import React from 'react';
import Avatar from '../Avatar/Avatar';
import BG from '../../assets/images/image-red.jpeg';

const TopImage = () => {
    return (
        <div className='relative h-72 flex justify-center'>
            <div className='h-full w-8/12 absolute'>
                <div className='relative top-0 right-0 block w-full h-full'>
                    <img
                        alt='Desktop'
                        className='object-cover min-w-full h-full'
                        src={BG}
                    />
                </div>
            </div>
            <Avatar />
        </div>
    );
};

export default TopImage;
